<template>
  <div class="home">
    <el-header class="header">
      <div class="menu-wrapper">
        <el-popover ref="menu" width="200" trigger="click" class="menu-panel">
          <el-button class="panel-button" style="width: 100%; margin: 0 auto" type="primary" size="small" @click="enterInfo">样板基础信息</el-button>
          <el-button class="panel-button" style="width: 100%; margin: 10px auto 0" type="primary" size="small" @click="enterStore">样板存货信息</el-button>
          <el-button class="panel-button" style="width: 100%; margin: 10px auto 0" type="primary" size="small" @click="enterLog" v-has="[user.GetOrderList]">订单查询</el-button>
          <el-button class="panel-button" style="width: 100%; margin: 10px auto 0"type="primary" size="small" @click="enterManager" v-has="[api.ShowUserInfo]">管理员配置</el-button>
        </el-popover>
        <el-button class="menu-button" v-popover:menu>菜单</el-button>
      </div>
      <div class="user-wrapper">
        <span class="text">Hello, {{ username }} !</span>
        <el-button class="header-button" type="danger" @click="handleLogout">登出</el-button>
      </div>
    </el-header>
    <el-container class="container">
      <el-aside class="aside">
        <el-card class="panel">
          <el-button class="panel-button" type="primary" @click="enterInfo">样板基础信息</el-button>
          <el-button class="panel-button" type="primary" @click="enterStore">样板存货信息</el-button>
          <el-button class="panel-button" type="primary" @click="enterLog" v-has="[user.GetOrderList]">订单查询</el-button>
          <el-button class="panel-button" type="primary" @click="enterManager" v-has="[api.ShowUserInfo]">管理员配置</el-button>
        </el-card>
      </el-aside>
      <el-main class="main">
        <router-view></router-view>
      </el-main>
    </el-container>
  </div>
</template>

<script>
  import * as api from 'api/manager'
  import * as user from 'api/index'
  import * as util from 'assets/js/util'
  import { mapGetters } from 'vuex'
  
  let myMixin = {
    data: function () {
      return {api, user}
    }
  }
  
  export default {
    name: "home",
    mixins: [myMixin],
    computed: {
      ...mapGetters([ 'username' ])
    },
    methods: {
      handleLogout() {
        this.$confirm('确认退出？', "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: 'info',
          center: true
        }).then(() => {
          let token = util.handleSession('token')
          console.log(token)
          user.Logout.r(token.token).then(res => {
            console.log(res)
            this.$message.success("注销成功")
          })
          this.$emit('logout')
        }).catch(() => {})
      },
      enterInfo() {
        this.$router.push('/info')
      },
      enterStore() {
        this.$router.push('/store')
      },
      enterManager() {
        this.$router.push('/manager')
      },
      enterLog() {
        this.$router.push('/log')
      }
    },
    created() {
      // location.reload()
      let token = util.handleSession('token')
      if (!token) {
        this.$router.push({ path: '/login' })
      }
    },
    watch: {
      $route(to, from) {
        if (from.path === '/login') {
          location.reload()
        }
      }
    }
  }
</script>

<style type="text/stylus" lang="stylus" rel="stylesheet/stylus" scoped>
  @import "~assets/stylus/variable.styl"
  @import "Home.styl"
</style>



